* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(245, 90, 90);
  color: #fff;
  box-shadow: 0 12px 2px 1px rgba(245, 90, 90, .2);
  padding: 10px 0;
}

nav h3 {
  margin-left: 6px;
  font-size: 20px;
  font-family: 'Lato', sans-serif;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Single Day', cursive;
}

nav img {
  width: 25px;
}

places {
  display: block;
}

.container {
  display: flex;
  padding: 70px 70px 0 75px;
}

.image {
  width: 250px;
  height: 300px;
}

.image img {
  width: 225px;
  height: 275px;
  border-radius: 20px;
}

.discription {
  margin: 15px;
  display: flex;
  flex-direction: column;
  align-items: start;
  height: 225px;
  width: 1000px;
}

.country {
  display: flex;
  width: 250px;
  justify-content: space-around;
}

.path {
  display: flex;
  width: 80px;
}

.country img {
  width: 10px;
}

.maps a {
  color: #918E9B;
}

h1 {
  font-size: 36px;
  font-weight: 700;
}

h4 {
  font-size: 18px;
  font-weight: 300;
}

.discription p {
  font-size: 15px;
  font-weight: 400;
}

places:last-child hr {
  display: none;
}